<template>
  <div>
   <!--  -->
    <el-card class="box-card" style="margin:20px 20px 0 20px;">
      <el-row>
        <el-col :span="24">
          <h5 style="border-bottom:1px solid #ece8e8;padding-bottom:15px;padding-left:15px"></h5>
        </el-col>

        <el-col style="padding-left:15px" :span="22">
          <el-col :span="4">
            <el-select
              filterable
              size="small"
              style=" width: 100%;"
              @clear="loadUserList()"
              @input="searchUser()"
              clearable
              v-model="unitId"
              placeholder=""
            >
              <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
          </el-col>
          <el-col style="margin-left:30px" :span="4">
            <el-select
              size="small"
              style=" width: 100%;"
              @clear="loadUserList()"
              @input="searchUser"
              clearable
              v-model="provinceCode"
              @change="function2(provinceCode)"
              placeholder=""
              :disabled="showCmd"
            >
              <el-option
                v-for="item in shengfen"
                :key="item.id"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-col>

          <el-col style="margin-left:30px" :span="4">
            <el-select
              size="small"
              :disabled="showCmd_1"
              style=" width: 100%;"
              @clear="loadUserList()"
              @input="searchUser"
              clearable
              v-model="cityCode"
              @change="function1(cityCode)"
              placeholder=""
            >
              <el-option v-for="item in level" :key="item.id" :label="item.name" :value="item.code"></el-option>
            </el-select>
          </el-col>

          <el-col style="margin-left:30px" :span="4">
            <el-select
              :disabled="showCmds"
              size="small"
              style=" width: 100%;"
              @clear="loadUserList()"
              @input="searchUser"
              clearable
              v-model="areaCode"
              placeholder=""
            >
              <el-option
                v-for="item in levels"
                :key="item.id"
                :label="item.name"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-col>
        </el-col>

        <el-button type="success" size="small" style="font-size:13px" @click="showAddUserDia()">
          <i style="padding-right:10px" class="el-icon-plus"></i>
        </el-button>
      </el-row>

      <!--  -->
      <template>
        <el-tabs style="margin-top:15px;" v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="" name="first">
            <el-table
              border
              :data="userlist"
              style="width: 100%;margin-top:-1px;padding-bottom:40px"
              :row-style="{height:'40px'}"
              :cell-style="{padding:'0px'}"
            >
              <el-table-column type="index" label="" width="50" show-overflow-tooltip>
                <template slot-scope="socpe">
                  <p style="cursor:pointer;" @click="showEdituser(socpe.row)">{{socpe.$index+1}}</p>
                </template>
              </el-table-column>
              <el-table-column show-overflow-tooltip prop="username" label=""></el-table-column>
              <el-table-column show-overflow-tooltip prop="unitName" label=""></el-table-column>
              <el-table-column prop="phone" label=""></el-table-column>
              <el-table-column show-overflow-tooltip prop="address" label=""></el-table-column>
              <el-table-column show-overflow-tooltip prop="code" label=""></el-table-column>
              <el-table-column show-overflow-tooltip prop="name" label=""></el-table-column>
              <el-table-column show-overflow-tooltip prop="note" label=""></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <!-- @change="changeMgStatus(scope.row)" -->
                  <!-- changeMgStatus  put -->
                  <!-- users/:uId/state/:type uidid -->

                  <el-switch
                    @change="changeMgStatus(scope.row)"
                    v-model="scope.row.isDisable"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                  ></el-switch>
                </template>
              </el-table-column>
              <!--  -->
              <el-table-column prop="address" width="150" label=" ">
                <template slot-scope="scope">
                  <el-tooltip
                    class="item"
                    effect="dark"
                    content=""
                    :enterable="false"
                    placement="top"
                  >
                    <el-button
                      type="success"
                      size="mini"
                      title=""
                      plain
                      icon="el-icon-check"
                      @click="showRole(scope.row.id)"
                      circle
                    ></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
          <el-tab-pane label="" name="second">
            <el-table
              border
              :row-style="{height:'40px'}"
              :cell-style="{padding:'0px'}"
              :data="userlist"
              style="width: 100%;margin-top:-1px;padding-bottom:40px"
            >
              <el-table-column type="index" label="" width="70" show-overflow-tooltip>
                <template slot-scope="socpe">
                  <p style="cursor:pointer;" @click="showEdituser(socpe.row)">{{socpe.$index+1}}</p>
                </template>
              </el-table-column>
              <el-table-column prop="username" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="unitName" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="phone" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="code" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="name" label="" show-overflow-tooltip></el-table-column>
              <el-table-column prop="note" label="" show-overflow-tooltip></el-table-column>
              <el-table-column label="">
                <template slot-scope="scope">
                  <el-switch
                    @change="changeMgStatus(scope.row)"
                    v-model="scope.row.isDisable"
                    active-color="#13ce66"
                    inactive-color="#ff4949"
                  ></el-switch>
                </template>
              </el-table-column>
              <!--  -->
              <el-table-column prop="address" width="150" label=" ">
                <template slot-scope="scope">
                  <el-tooltip
                    class="item"
                    effect="dark"
                    content=""
                    :enterable="false"
                    placement="top"
                  >
                    <el-button
                      type="success"
                      size="mini"
                      title=""
                      plain
                      icon="el-icon-check"
                      @click="showRole(scope.row.id)"
                      circle
                    ></el-button>
                  </el-tooltip>
                </template>
              </el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </template>
      <el-pagination
        style="border:1px solid #ebeef5;padding:5px 15px;display:flex; justify-content: flex-end;"
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="page"
        :page-sizes="[10,15,20,25,30]"
        :page-size="10"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <!--  -->
      <el-dialog
        title=""
        v-dialogDrag
        :close-on-click-modal="false"
        :visible.sync="dialogFormVisibleAdd"
        width="80%"
      >
        <el-form :model="form" :rules="rules" ref="form">
          <el-col :span="24">
            <el-col :span="8">
              <el-form-item prop="vestingProvinceId" label-width="200px" label=":">
                <el-select
                  v-model="form.provinceCode"
                  @change="function2(form.provinceCode)"
                  :disabled="showCmd"
                  placeholder=""
                >
                  <el-option
                    v-for="item in shengfen"
                    :key="item.id"
                    :label="item.name"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="cityCode" label-width="200px" label=":">
                <el-select
                  v-model="form.cityCode"
                  @change="function1(form.cityCode)"
                  :disabled="showCmd_1"
                  placeholder=""
                >
                  <el-option
                    v-for="item in level"
                    :key="item.id"
                    :label="item.name"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item prop="areaCode" label-width="200px" label=":">
                <el-select v-model="form.areaCode" :disabled="showCmds" placeholder="">
                  <el-option
                    v-for="item in levels"
                    :key="item.id"
                    :label="item.name"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-col>
          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label="" class="red_s" label-width="200px" prop="username">
                <el-input v-model="form.username" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="" class="red_s" label-width="200px" prop="unitId">
                <el-select
                  style=" width: 100%;"
                  filterable
                  @clear="loadUserList()"
                  @change="searchUser()"
                  clearable
                  v-model="form.unitId"
                  placeholder=""
                >
                  <el-option
                    v-for="item in options"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-col>

          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label="" label-width="200px" >
                <el-input v-model="form.idCard" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="" label-width="200px" >
                <el-input v-model="form.phone" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>

          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label="" class="red_s" label-width="200px" prop="code">
                <el-input v-model="form.code" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="" class="red_s" label-width="200px" prop="name">
                <el-input v-model="form.name" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>

          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label=""  label-width="200px" prop="address">
                <el-input v-model="form.address" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="" label-width="200px" prop="note">
                <el-input v-model="form.note" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleAdd = false"> </el-button>
          <el-button type="primary" @click="AddUser()"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        v-dialogDrag
        :close-on-click-modal="false"
        width="80%"
        :visible.sync="dialogFormVisibleEdit"
      >
        <el-form :model="editUserForms" :rules="rules" ref="editUserForms">
          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label="" label-width="200px" prop="username">
                <el-input disabled v-model="editUserForms.username" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="" label-width="200px" prop="code">
                <el-input v-model="editUserForms.code" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>

          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label="" label-width="200px" prop="unitId">
                <el-select
                  filterable
                  size="small"
                  style=" width: 100%;"
                  @clear="loadUserList()"
                  @change="searchUser()"
                 
                  clearable
                  v-model="editUserForms.unitId"
                  placeholder=""
                >
                  <el-option
                    v-for="item in options"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label="" label-width="200px" prop="phone">
                <el-input v-model="editUserForms.phone" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>

          <el-col :span="24">
            <el-col :span="10">
              <el-form-item label="" label-width="200px" prop="name">
                <el-input v-model="editUserForms.name" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item label=""  label-width="200px" prop="address">
                <el-input v-model="editUserForms.address" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>

          <el-col :span="24">
            
             <el-col :span="10">
               <el-form-item label="" label-width="200px" prop="note">
                <el-input v-model="editUserForms.note" autocomplete="off"></el-input>
              </el-form-item>
              
            </el-col>
            

            <el-col :span="2">&nbsp;</el-col>
            <el-col :span="10">
              <el-form-item hidden label="id" label-width="200px" prop="id">
                <el-input v-model="editUserForms.id" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item hidden label="" label-width="200px">
                <el-input v-model="editUserForms.isDisable" autocomplete="off"></el-input>
              </el-form-item>
            </el-col>
          </el-col>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleEdit = false"> </el-button>
          <el-button type="primary" @click="EditUser()"> </el-button>
        </div>
      </el-dialog>

      <!--  -->
      <el-dialog
        title=""
        v-dialogDrag
        :close-on-click-modal="false"
        :visible.sync="dialogFormVisibleRole"
      >
        <p style="margin:5px 2px;border-bottom: 1px solid gainsboro;">:</p>
        <p>
          <el-checkbox-group :min="0" :max="1" size="small" v-model="types">
            <el-checkbox
              style="margin-top:5px"
              border
              @change="handleCheckAllChange(item)"
              v-for="item in rolelist"
              :label="item.id"
              :key="item.id"
            >{{item.name}}</el-checkbox>
          </el-checkbox-group>
        </p>
        <!-- <p style="margin:5px 2px;border-bottom: 1px solid gainsboro;">:</p>
        <p>
          <el-checkbox-group size="small" v-model="typess">
            <el-checkbox
              style="margin-top:5px"
              border
              @change="handleCheckAllChanges(item)"
              v-for="item in rolelists"
              :label="item.id"
              :key="item.id"
            >{{item.name}}</el-checkbox>
          </el-checkbox-group>
        </p> -->

        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisibleRole = false"> </el-button>
          <el-button type="primary" @click="roleUser()"> </el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  name: "usernames",
  data() {
    return {
      showCmd: true,
      showCmds: true,
      showCmd_1: true,

      uid: "",
      cid: "",
      aid: "",
      checkAll: false,
      types: [], //id
      typess: [],
      rolelist: [],
      checkEquipArr: [],
      isIndeterminate: true,
      activeName: "first", //tab
      mo: true,
      options: [],
      unitId: "",
      Id: "",
      id: "",
      cityCode: "",
      areaCode: "",
      provinceCode: "",
      shengfen: [],
      level: [],
      levels: [],
      userlist: [], //
      //
      total: -1,
      rows: 10,
      page: 1,
      isDisable: 0,
      // 
      dialogFormVisibleAdd: false,
      dialogFormVisibleEdit: false,
      dialogFormVisibleRole: false,
      //
      form: {
        provinceCode: "", //
        cityCode: "", //
        areaCode: "", //
        username: "",
        unitId: "",
        idCard: "",
        phone: "",
        code: "",
        name: "",
        note: "",
        address: "",
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        unitId: [
          { required: true, message: "", trigger: "blur" },
        ],
        idCard: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: "",
          },
        ],
        phone: [
          { required: true, message: "", trigger: "blur" },
          {
            pattern: /^1[3456789]\d{9}$/,
            message: "",
          },
        ],
        code: [
          { required: true, message: "", trigger: "blur" },
        ],
        name: [{ required: true, message: "", trigger: "blur" }],
        // note: [{ required: true, message: "", trigger: "blur" }]
      },
      ROleid: -1,
      // 
      editUserForms: {
        id: "",
        username: "",
        unitId: "",
        phone: "",
        code: "",
        name: "",
        note: "",
        address:""
      },
      tyPe:"",
      provincecode: "",
      citycode: "",
      areacode: "",
      ids: [],
      rolelists: "",
      checkEquipArrs: [],
      checkEquipArrss: [],
      roleuser: [],
    };
  },
  watch: {
    dialogFormVisibleAdd: function (val, oldVla) {
      this.$refs["form"].resetFields();
    },

    dialogFormVisibleAdd: function (val, oldVla) {
      this.$refs["editUserForms"].resetFields();
    },
  },
created() {
    this.getUserlist();
    this.selectedDia();
  },
  methods: {
    // 
    async zshi() {
      const res = await this.$http.get(`/role/findUserId/${this.ROleid}`);
      console.log(res);
      if (res.data.code == 0) {
        let roleids = res.data.data;
        let arr = [];
        roleids.forEach((item) => {
          arr.push(item.id);
        });
        this.types = arr;
        console.log(this.types);
        this.qphx();
      }
    },
    // 
    async qphx() {
      const ress = await this.$http.get(
        `/role-permission/bottle/${this.types}`
      );
      console.log(ress);
      if (ress.data.code == 0) {
        if (ress.data.data == null) {
          return;
        } else {
          let roleids = ress.data.data;
          let arr = [];
          roleids.forEach((item) => {
            arr.push(item.id);
          });
          this.typess = arr;
          console.log(this.typess);
        }
      }
    },
    async qip() {
      const res = await this.$http.get(`Permission/findBottle`);
      console.log(res);
      this.rolelists = res.data;
    },
    // 
    async showRole(roleID) {
      this.checkEquipArr = [];
      this.typess = [];
      this.ROleid = roleID;
      this.dialogFormVisibleRole = true;
      const res = await this.$http.get(`/role/findPage?page=0&rows=25`);
      console.log(res);
      this.rolelist = res.data.data.list;
      this.zshi();
      // this.qphx();
      this.qip();
    },

    handleCheckAllChange(item) {
      this.checkEquipArrss = [];
      let ids = item.id;
      let arr2 = [];
      let arr = this.types;
      // console.log(arr);
      // arr.push = ids;
      for (let i = 0; i < arr.length; i++) {
        arr2.push(arr[i]);
      }
      this.checkEquipArrss = arr2;
      this.typess = arr2;
      console.log(this.typess); //id
    },
    handleCheckAllChanges(item) {
      let ids = item.id;
      // console.log(ids);
      let arr_2 = [];
      let arr_1 = this.typess;
      // arr_1.push = ids;
      for (var i = 0; i < arr_1.length; i++) {
        arr_2.push(arr_1[i]);
      }
      this.checkEquipArrs = arr_2;
      console.log(this.checkEquipArrs); //id
    },

    // 
    async roleUser() {
      this.checkEquipArr = this.checkEquipArrs;
      var idss = [];

      var types = this.types;

      for (var i = 0; i < this.types.length; i++) {
        idss.push(this.types[i]);
      }
      this.roleuser = idss;
      // this.roleuser = [...this.types, ...this.checkEquipArrss];
      console.log(this.roleuser);
      const res = await this.$http.post(
        `/user-role/${this.ROleid}?roleId=${this.roleuser}`,
        this.typess
      );
      console.log(res);
      if (res.data.code == 0) {
        this.$message({
          type: "success",
          message: res.data.msg,
        });
        this.dialogFormVisibleRole = false;

        this.getUserlist();
      } else if (res.data.code == 1) {
        this.$message({
          type: "warning",
          message: res.data.msg,
        });
      }
    },

    //tab
    handleClick(tab, event) {
      console.log(tab);
      if (tab.name === "first") {
        this.isDisable = 0;
        this.getUserlist();
      } else {
        this.isDisable = 1;
        this.getUserlist();
      }
    },

    async changeMgStatus(val) {
      console.log(val);

      if (val.isDisable == 1) {
        const res = await this.$http.put(`/user/${val.id}/enabled`);
        console.log(res);
        this.$message({
          type: "success",
          message: "",
        });
        this.getUserlist();
        console.log(res);
      } else {
        const res = await this.$http.put(`/user/${val.id}/disable`);
        this.$message({
          type: "success",
          message: "",
        });
        this.getUserlist();
        console.log(res);
      }
    },
    //  
    async function2(val) {
      this.code = val;
      console.log(this.code);
      this.cityCode=""
      this.areaCode=""
      this.form.cityCode=""
      this.form.areaCode=""
      const res = await this.$http.get(`/chinese/city/${this.code}`);
      console.log(res);
      this.level = res.data.data;
      this.getUserlist();
    },
    // 
    async function1(val) {
      this.Id = val;
